:root{
    --gap:7px;
    --width:70px;
    --height:70px;
}


.screen{
    width:320px;
    height:100vh;
    background-color: white;
    margin-inline:auto;

    display:flex;
    justify-content:center;
    align-items: center;
}

.board{
    background-color: black;

    display:grid;
    gap:var(--gap);

    grid-template-columns:auto auto auto;
    grid-template-rows:auto auto auto;
}

.box{
    height: var(--height);
    width: var(--width);
    background-color: white;
}

@media(min-width:320px) and (max-width:479px) and (orientation:portrait){
    /* 148x320 (19.5:9 aspect ratio) */
    :root{
        --gap:7px;
        --width:70px;
        --height:70px;
    }
}

@media(min-width:480px) and (max-width:767px) and (orientation:portrait){
    /* 222x480 (19.5:9 aspect ratio) */
    :root{
        --gap:1px;
        --width:100px;
        --height:100px;
    }
}

@media(min-width:320px) and (max-width:479px) and (orientation:landscape){
    /* 320x148 (19.5:9 aspect ratio) */
    :root{
        --gap:3px;
        --width:30px;
        --height:30px;
    }
}

@media(min-width:480px) and (max-width:767px) and (orientation:landscape){
    /* 480x222 (19.5:9 aspect ratio) */
    :root{
        --gap:5px;
        --width:50px;
        --height:50px;
    }
}

@media(min-width:768px) and (max-width:1023px){
    /* 768x576 (4:3 aspect ratio) */
    :root{
        --gap:13px;
        --width:130px;
        --height:130px;
    }
}

@media(min-width:1024px) and (max-width:1279px){
    /* 1024x768 (4:3 aspect ratio) */
    :root{
        --gap:17px;
        --width:170px;
        --height:170px;
    }
}

@media(min-width:1280px) and (max-width:1439px){
    /* 1280x720 (16:9 aspect ratio) */
    :root{
        --gap:16px;
        --width:160px;
        --height:160px;
    }
}

@media(min-width:1440px) and (max-width:1919px){
    /* 1440x810 (16:9 aspect ratio) */
    :root{
        --gap:18px;
        --width:180px;
        --height:180px;
    }
}

@media(min-width:1920px){
    /* 1920x1080 (16:9 aspect ratio) */
    :root{
        --gap:25px;
        --width:250px;
        --height:250px;
    }
}
